import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import SalaryComputationStaffList from "./SalaryComputationStaffList";
import { useCalendar } from "../../../Hooks/CustomHooks/use-calendar";
import { useState } from "react";
import dayjs from "dayjs";
import SelectFinancialYear from "../SelectFinancialYear";
import PayrollSalaryComputationConfigMonthCard from "./PayrollSalaryComputationConfigMonthCard";
import PayrollSalarySlip from "./PayrollSalarySlip/PayrollSalarySlip";
const PayrollSalaryComputationConfig = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { selectedDate, setSelectedDate } = useCalendar();
  const [financialYear, setFinancialYear] = useState(false);

  const onYearToggle = () => {
    setFinancialYear((pre) => !pre);
  };

  const onYearChoose = (value) => {
    setSelectedDate(
      (prev) => new Date(`${prev.getMonth() + 1} ${prev.getDate()} ${value}`)
    );
    onYearToggle();
  };

  const onBack = () => {
    navigate(`/q/${params.username}/member/staff/payroll`, {
      state: {
        ...getQuery.state,
        name: "",
        staffId: "",
      },
    });
  };
  return (
    <>
      {searchParams.get("n") === "year" ? (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <div className={style.fm_header_start_container}>
                <img
                  alt="search icon"
                  src={`${assestsUtilityUrl}/back.svg`}
                  style={{
                    height: "2rem",
                    width: "2rem",
                  }}
                  onClick={onBack}
                />
                <h6>{t("salary_computation")}</h6>
              </div>
              <div className={style.fm_header_start_container}>
                <h6
                  style={{
                    cursor: "pointer",
                  }}
                  onClick={onYearToggle}
                >
                  {t("financial_year")} : {dayjs(selectedDate).format("YYYY")}
                </h6>
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            {monthList?.map((mt) => (
              <PayrollSalaryComputationConfigMonthCard
                key={mt?.name}
                mt={mt}
                financialYear={dayjs(selectedDate).format("YYYY")}
              />
            ))}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <div className={style.fm_header_start_container}>
                <img
                  alt="search icon"
                  src={`${assestsUtilityUrl}/back.svg`}
                  style={{
                    height: "2rem",
                    width: "2rem",
                  }}
                  onClick={onBack}
                />
                <h6>
                  {t("salary_computation")} of{" "}
                  {getQuery.state?.headCard?.name ?? ""},{" "}
                  {getQuery.state?.financialYear ?? ""}
                </h6>
              </div>
            </div>
          </UserMemberContentWrapper>

          <HorizontalTabWrapper>
            {tabList.map((tab) => (
              <HorizontalTabCard
                key={tab?.id}
                tab={tab}
                isLink
                isActive={tab.groupIs?.includes(searchParams.get("n"))}
                parentState={getQuery.state}
                url={`/q/${params.username}/member/staff/payroll?type=salary-computation${tab.urlKey}`}
              />
            ))}
          </HorizontalTabWrapper>
          <BorderBottom
            customStyleBorder={{
              marginBlock: "0",
            }}
          />
          <UserMemberContentWrapper>
            {searchParams.get("n") === "days" ? (
              <SalaryComputationStaffList
                instituteId={instituteId}
                initalUrl="salary-days"
              />
            ) : searchParams.get("n") === "computation" ? (
              <SalaryComputationStaffList
                instituteId={instituteId}
                initalUrl="COMPUTATION"
              />
            ) : searchParams.get("n") === "slip" ? (
              <PayrollSalarySlip pid={pid} />
            ) : (
              <SalaryComputationStaffList
                instituteId={instituteId}
                initalUrl="attendance-sheet"
              />
            )}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}
      {financialYear && (
        <SelectFinancialYear
          onClose={onYearToggle}
          onYearChoose={onYearChoose}
        />
      )}
    </>
  );
};

export default PayrollSalaryComputationConfig;

const tabList = [
  {
    label: "attendance_sheet",
    id: "attendance_sheet",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "salary_days",
    id: "salary_days",
    urlKey: "&n=days",
    groupIs: ["days"],
  },
  {
    label: "salary_computation",
    id: "salary_computation",
    urlKey: "&n=computation",
    groupIs: ["computation"],
  },
  {
    label: "salary_slip",
    id: "salary_slip",
    urlKey: "&n=slip",
    groupIs: ["slip"],
  },
];

