import { useTranslation } from "react-i18next";
import style from "../../../StoreManager/StoreManager.module.css";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
const PayrollSalarySlipCard = ({ sSlip }) => {
  const { t } = useTranslation();

  const onSalarySlip = () => {
    if (sSlip?.key) {
      const newWindow = window.open(
        `${pdfCdnUrl}/${sSlip?.key}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  };
  return (
    <>
      <div className={style.smc_card}>
        <div
          className={style.smc_card_contatiner}
          style={{
            justifyContent: "space-between",
            width: "100%",
          }}
          // onClick={onToggle}
        >
          <div className={style.smc_card_contatiner}>
            <img
              src={
                sSlip?.staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${sSlip?.staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                height: "2.3rem",
                width: "2.3rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.smc_card_contatiner_inner}>
              <h6>{`${sSlip?.staff?.staffFirstName ?? ""} ${
                sSlip?.staff?.staffMiddleName ?? ""
              } ${sSlip?.staff?.staffLastName ?? ""}`}</h6>
              <p>
                {t("index_number_1")} {sSlip?.staff?.staffROLLNO ?? ""}
              </p>
            </div>
          </div>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {" "}
                {t("net_pay")}: {t("rs_only")} {sSlip?.net_payable ?? ""}
              </h6>
              <p>
                {t("date")}: {dayjs(sSlip?.created_at)?.format("DD/MM/YYYY")}
              </p>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/receipt.svg`}
              alt="receipt icon"
              title="Download Salary Slip"
              style={{
                width: "auto",
                height: "1.6rem",
              }}
              onClick={onSalarySlip}
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default PayrollSalarySlipCard;
