import { Link, useLocation, useParams } from "react-router-dom";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../../StoreManager/StoreManager.module.css";
import { useTranslation } from "react-i18next";
import SalaryComputationStaffStructure from "./SalaryComputation/SalaryComputationStaffStructure";
import { useState } from "react";
const SalaryComputationStaffListCard = ({ staff, initalUrl }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const [open, setOpen] = useState(false);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      {initalUrl === "COMPUTATION" ? (
        <div className={style.smc_card} onClick={onToggle}>
          <div
            className={style.smc_card_contatiner}
            style={{
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <div className={style.smc_card_contatiner}>
              <img
                src={
                  staff?.staffProfilePhoto
                    ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                    : `${assestsFeedUrl}/feed-user-avatar.svg`
                }
                loading="lazy"
                alt="avatar"
                style={{
                  height: "2.3rem",
                  width: "2.3rem",
                  borderRadius: "50%",
                }}
              />

              <div className={style.smc_card_contatiner_inner}>
                <h6>{`${staff?.staffFirstName ?? ""} ${
                  staff?.staffMiddleName ?? ""
                } ${staff?.staffLastName ?? ""}`}</h6>
                <p>
                  {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                </p>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <Link
          to={`/q/${params.username}/member/staff/payroll?type=${initalUrl}`}
          state={{
            ...getQuery.state,
            name: `${staff?.staffFirstName ?? ""} ${
              staff?.staffMiddleName ?? ""
            } ${staff?.staffLastName ?? ""}`,
            staffId: staff?._id,
          }}
        >
          <div className={style.smc_card}>
            <div
              className={style.smc_card_contatiner}
              style={{
                justifyContent: "space-between",
                width: "100%",
              }}
            >
              <div className={style.smc_card_contatiner}>
                <img
                  src={
                    staff?.staffProfilePhoto
                      ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  loading="lazy"
                  alt="avatar"
                  style={{
                    height: "2.3rem",
                    width: "2.3rem",
                    borderRadius: "50%",
                  }}
                />

                <div className={style.smc_card_contatiner_inner}>
                  <h6>{`${staff?.staffFirstName ?? ""} ${
                    staff?.staffMiddleName ?? ""
                  } ${staff?.staffLastName ?? ""}`}</h6>
                  <p>
                    {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Link>
      )}

      {open && (
        <SalaryComputationStaffStructure
          onClose={onToggle}
          ssid={staff?.salary_structure}
          // ssid={"661cbb816691502f8442b153"}
          staff={staff}
        />
      )}
    </>
  );
};

export default SalaryComputationStaffListCard;
