import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import { useEffect, useState } from "react";
import {
  usePayrollOneSalaryStructure,
  usePayrollStaffEditSalaryStructure,
  usePayrollStaffSalaryCompute,
} from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useLocation } from "react-router-dom";
import SalaryComputationStaffComputedStructure from "./SalaryComputationStaffComputedStructure";
const SalaryComputationStaffStructure = ({ staff, onClose, ssid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [state, setState] = useState({
    sal_com: [],
    employee_ded: [],
    employar_ded: [],
    com: [],
  });
  const [stateTotal, setStateTotal] = useState({
    sal_com: 0,
    employee_ded: 0,
    employar_ded: 0,
    com: 0,
  });
  const [computedData, setComputedData] = useState(null);
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [payrollStaffEditSalaryStructure] =
    usePayrollStaffEditSalaryStructure();

  const [payrollStaffSalaryCompute] = usePayrollStaffSalaryCompute();

  const { payrollOneSalaryStructure } = usePayrollOneSalaryStructure({
    data: {
      ssid: ssid,
    },
    skip: !ssid,
  });

  useEffect(() => {
    if (payrollOneSalaryStructure?.salary_components?.length) {
      let tp = 0;
      let arr = [];
      for (let st of payrollOneSalaryStructure?.salary_components) {
        tp += st?.head_amount;
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master?._id,
          // _id: st?._id,
          // created_at: st?.created_at,
        });
      }
      setStateTotal((prev) => ({
        ...prev,
        sal_com: tp,
      }));
      setState((prev) => ({
        ...prev,
        sal_com: arr,
      }));
    }

    if (payrollOneSalaryStructure?.employee_deduction?.length) {
      let tp = 0;
      let arr = [];

      for (let st of payrollOneSalaryStructure?.employee_deduction) {
        tp += st?.head_amount;
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master?._id,
          // _id: st?._id,
          // created_at: st?.created_at,
        });
      }
      setStateTotal((prev) => ({
        ...prev,
        employee_ded: tp,
      }));
      setState((prev) => ({
        ...prev,
        employee_ded: arr,
      }));
    }

    if (payrollOneSalaryStructure?.employar_deduction?.length) {
      let tp = 0;
      let arr = [];

      for (let st of payrollOneSalaryStructure?.employar_deduction) {
        tp += st?.head_amount;
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master?._id,
          // _id: st?._id,
          // created_at: st?.created_at,
        });
      }
      setStateTotal((prev) => ({
        ...prev,
        employar_ded: tp,
      }));
      setState((prev) => ({
        ...prev,
        employar_ded: arr,
      }));
    }

    if (payrollOneSalaryStructure?.compliances?.length) {
      let tp = 0;
      let arr = [];

      for (let st of payrollOneSalaryStructure?.compliances) {
        tp += st?.head_amount;
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master?._id,
          // _id: st?._id,
          // created_at: st?.created_at,
        });
      }
      setStateTotal((prev) => ({
        ...prev,
        com: tp,
      }));
      setState((prev) => ({
        ...prev,
        com: arr,
      }));
    }
  }, [
    payrollOneSalaryStructure?.salary_components,
    payrollOneSalaryStructure?.employar_deduction,
    payrollOneSalaryStructure?.employee_deduction,
    payrollOneSalaryStructure?.compliances,
  ]);

  useEffect(() => {
    if (state.sal_com?.length) {
      let tp = 0;
      for (let st of state.sal_com) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        sal_com: tp,
      }));
    }

    if (state.employee_ded?.length) {
      let tp = 0;
      for (let st of state.employee_ded) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        employee_ded: tp,
      }));
    }

    if (state.employar_ded?.length) {
      let tp = 0;
      for (let st of state.employar_ded) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        employar_ded: tp,
      }));
    }

    if (state.com?.length) {
      let tp = 0;
      for (let st of state.com) {
        tp += st?.head_amount;
      }
      setStateTotal((prev) => ({
        ...prev,
        com: tp,
      }));
    }
  }, [state.sal_com, state.employar_ded, state.employee_ded, state.com]);
  const onFeeHeadPriceChange = (e, index, flow) => {
    if (flow === "SALARY_COMPONENTS") {
      const input1 = [...state.sal_com];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        sal_com: input1,
      }));
    } else if (flow === "EMPLOYEE_DEDUCTION") {
      const input1 = [...state.employee_ded];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        employee_ded: input1,
      }));
    } else if (flow === "EMPLOYAR_DEDUCTION") {
      const input1 = [...state.employar_ded];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        employar_ded: input1,
      }));
    } else if (flow === "COMPLIANCES") {
      const input1 = [...state.com];
      for (let inp = 0; inp < input1?.length; inp++) {
        if (inp === index) {
          input1[inp].head_amount = +e.target.value;
        }
      }
      setState((prev) => ({
        ...prev,
        com: input1,
      }));
    } else {
    }
  };

  const onEditUpdate = () => {
    if (staff?._id && ssid) {
      setDisabled((pre) => !pre);
      payrollStaffEditSalaryStructure({
        sid: staff?._id,
        bodyParameter: {
          compliances: state.com,
          employar_deduction: state.employar_ded,
          employee_deduction: state.employee_ded,
          salary_components: state.sal_com,
          srid: ssid,
        },
      })
        .then(() => {
          setStep(1);
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCompute = () => {
    if (
      staff?._id &&
      getQuery.state?.financialYear &&
      getQuery.state?.headCard?.value
    ) {
      setDisabled((pre) => !pre);
      payrollStaffSalaryCompute({
        sid: staff?._id,
        month: getQuery.state?.headCard?.value,
        year: getQuery.state?.financialYear,
      })
        .then((res) => {
          setComputedData(res.data);
          setStep(2);
          setDisabled((pre) => !pre);
        })
        .catch({});
      // console.info("call compute");
    }
  };

  return (
    <>
      {step === 2 ? (
        <SalaryComputationStaffComputedStructure
          computedData={computedData}
          onClose={() => setStep(1)}
          staff={staff}
          onParentClose={onClose}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {`${staff?.staffFirstName ?? ""} ${
                      staff?.staffMiddleName ?? ""
                    } ${staff?.staffLastName ?? ""} `}
                    - {t("salary_computation")}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <section
                className={style.table_container}
                style={{
                  margin: "0",
                }}
              >
                <div className={style.table_heading}>
                  <h6>{t("particulars")}</h6>
                  <h6>{t("amount")}</h6>
                </div>
                <div className={style.table_body}>
                  {state.sal_com?.map((afh, index) => (
                    <div className={style.table_body_list} key={index}>
                      <h6>{afh?.head_name}</h6>
                      <input
                        type={"number"}
                        value={afh?.head_amount}
                        onChange={(e) =>
                          onFeeHeadPriceChange(e, index, "SALARY_COMPONENTS")
                        }
                        className={style.fee_head_amount_input}
                      />
                    </div>
                  ))}
                </div>
                <div className={style.table_body}>
                  <div
                    className={style.table_body_list}
                    style={{
                      borderTopColor: "rgba(18, 18, 18, 0.5)",
                      borderTopWidth: "0.5px",
                      borderTopStyle: "solid",
                    }}
                  >
                    <h6
                      className={style.fees_total}
                      style={{
                        fontWeight: "700",
                      }}
                    >
                      {t("total_earning")}
                    </h6>
                    <h6
                      className={style.fees_total}
                      style={{
                        fontWeight: "700",
                      }}
                    >
                      {stateTotal.sal_com}
                    </h6>
                  </div>
                </div>
              </section>
              <div className={style.table_body}>
                {state.employee_ded?.map((gfh, index) => (
                  <div className={style.table_body_list} key={index}>
                    <h6>{gfh?.head_name}</h6>
                    <input
                      type={"number"}
                      value={gfh?.head_amount}
                      onChange={(e) =>
                        onFeeHeadPriceChange(e, index, "EMPLOYEE_DEDUCTION")
                      }
                      className={style.fee_head_amount_input}
                    />
                  </div>
                ))}
              </div>

              <div className={style.table_body}>
                <div
                  className={style.table_body_list}
                  style={{
                    borderTopColor: "rgba(18, 18, 18, 0.5)",
                    borderTopWidth: "0.5px",
                    borderTopStyle: "solid",
                  }}
                >
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {t("total_pay")}
                  </h6>
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {stateTotal.sal_com - stateTotal.employee_ded}
                  </h6>
                </div>
              </div>
              <div className={style.table_body}>
                {state.com?.map((gfh, index) => (
                  <div className={style.table_body_list} key={index}>
                    <h6>{gfh?.head_name}</h6>
                    <input
                      type={"number"}
                      value={gfh?.head_amount}
                      onChange={(e) =>
                        onFeeHeadPriceChange(e, index, "COMPLIANCES")
                      }
                      className={style.fee_head_amount_input}
                    />
                  </div>
                ))}
              </div>

              <div className={style.table_body}>
                <div
                  className={style.table_body_list}
                  style={{
                    borderTopColor: "rgba(18, 18, 18, 0.5)",
                    borderTopWidth: "0.5px",
                    borderTopStyle: "solid",
                  }}
                >
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {t("net_pay")}
                  </h6>
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {stateTotal.sal_com -
                      stateTotal.employee_ded -
                      stateTotal.com}
                  </h6>
                </div>
              </div>

              <div className={style.table_body}>
                {state.employar_ded?.map((gfh, index) => (
                  <div className={style.table_body_list} key={index}>
                    <h6>{gfh?.head_name}</h6>
                    <input
                      type={"number"}
                      value={gfh?.head_amount}
                      onChange={(e) =>
                        onFeeHeadPriceChange(e, index, "EMPLOYAR_DEDUCTION")
                      }
                      className={style.fee_head_amount_input}
                    />
                  </div>
                ))}
              </div>

              <div className={style.table_body}>
                <div
                  className={style.table_body_list}
                  style={{
                    borderTopColor: "rgba(18, 18, 18, 0.5)",
                    borderTopWidth: "0.5px",
                    borderTopStyle: "solid",
                  }}
                >
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {t("ctc")}
                  </h6>
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {stateTotal.sal_com -
                      stateTotal.employee_ded +
                      stateTotal.employar_ded}
                  </h6>
                </div>
              </div>

              {step === 1 ? (
                <CreateButton label="compute" onAction={onCompute} />
              ) : (
                <CreateButton label="update" onAction={onEditUpdate} />
              )}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default SalaryComputationStaffStructure;
