import { useCallback, useEffect, useState } from "react";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import { usePayrollAllSalarySlip } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import { useLocation } from "react-router-dom";
import PayrollSalarySlipCard from "./PayrollSalarySlipCard";

const PayrollSalarySlip = ({ pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { payrollAllSalarySlip, payrollAllSalarySlipLoading } =
    usePayrollAllSalarySlip({
      data: {
        page: page,
        limit: 10,
        search: search,
        pid: pid,
        month: getQuery.state?.headCard?.value,
        year: getQuery.state?.financialYear,
      },
      skip: !pid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (payrollAllSalarySlip?.list?.length) {
      let obj = nextPage(payrollAllSalarySlip?.list?.length);
      setState(() => obj);
    }
  }, [payrollAllSalarySlip?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>
      {payrollAllSalarySlip?.list?.map((sSlip, index) =>
        payrollAllSalarySlip?.list?.length === index + 1 ? (
          <div key={sSlip?._id} ref={ref}>
            <PayrollSalarySlipCard sSlip={sSlip} />
          </div>
        ) : (
          <PayrollSalarySlipCard sSlip={sSlip} key={sSlip?._id} />
        )
      )}
      {payrollAllSalarySlipLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        payrollAllSalarySlip?.list?.length === 0 && (
          <EmptyMessage title="No salary slips found related this search." />
        )
      ) : (
        payrollAllSalarySlip?.list?.length === 0 && (
          <EmptyMessage title="No salary slips." />
        )
      )}
    </>
  );
};

export default PayrollSalarySlip;
