import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import { useEffect, useState } from "react";
import { usePayrollStaffSalaryFinalize } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useLocation } from "react-router-dom";
const SalaryComputationStaffComputedStructure = ({
  staff,
  onClose,
  computedData,
  onParentClose,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [state, setState] = useState({
    sal_com: [],
    employee_ded: [],
    employar_ded: [],
    com: [],
  });

  const [disabled, setDisabled] = useState(false);

  const [payrollStaffSalaryFinalize] = usePayrollStaffSalaryFinalize();

  useEffect(() => {
    if (computedData?.structure?.salary_components?.length) {
      let arr = [];
      for (let st of computedData?.structure?.salary_components) {
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master,
          _id: st?._id,
          created_at: st?.created_at,
        });
      }

      setState((prev) => ({
        ...prev,
        sal_com: arr,
      }));
    }

    if (computedData?.structure?.employee_deduction?.length) {
      let arr = [];

      for (let st of computedData?.structure?.employee_deduction) {
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master,
          _id: st?._id,
          created_at: st?.created_at,
        });
      }

      setState((prev) => ({
        ...prev,
        employee_ded: arr,
      }));
    }

    if (computedData?.structure?.employar_deduction?.length) {
      let arr = [];

      for (let st of computedData?.structure?.employar_deduction) {
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master,
          _id: st?._id,
          created_at: st?.created_at,
        });
      }

      setState((prev) => ({
        ...prev,
        employar_ded: arr,
      }));
    }

    if (computedData?.structure?.compliances?.length) {
      let arr = [];

      for (let st of computedData?.structure?.compliances) {
        arr.push({
          head_name: st?.master?.heads_name,
          head_amount: st?.head_amount,
          master: st?.master,
          _id: st?._id,
          created_at: st?.created_at,
        });
      }

      setState((prev) => ({
        ...prev,
        com: arr,
      }));
    }
  }, [
    computedData?.structure?.salary_components,
    computedData?.structure?.employar_deduction,
    computedData?.structure?.employee_deduction,
    computedData?.structure?.compliances,
  ]);

  const onFinalize = () => {
    if (staff?._id && getQuery.state?.pid) {
      setDisabled((pre) => !pre);
      payrollStaffSalaryFinalize({
        sid: staff?._id,
        bodyParameter: {
          pid: getQuery.state?.pid,
          month: getQuery.state?.headCard?.value,
          year: getQuery.state?.financialYear,
          salary_struct: computedData?.structure,
          custom_obj: computedData?.custom_obj,
        },
      })
        .then(() => {
          onParentClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>
                  {`${staff?.staffFirstName ?? ""} ${
                    staff?.staffMiddleName ?? ""
                  } ${staff?.staffLastName ?? ""} `}
                  - {t("salary_computation")}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <section
              className={style.table_container}
              style={{
                margin: "0",
              }}
            >
              <div className={style.table_heading}>
                <h6>{t("particulars")}</h6>
                <h6>{t("amount")}</h6>
              </div>
              <div className={style.table_body}>
                {state.sal_com?.map((afh, index) => (
                  <div className={style.table_body_list} key={index}>
                    <h6>{afh?.head_name}</h6>
                    <h6>{afh?.head_amount}</h6>
                  </div>
                ))}
              </div>
              <div className={style.table_body}>
                <div
                  className={style.table_body_list}
                  style={{
                    borderTopColor: "rgba(18, 18, 18, 0.5)",
                    borderTopWidth: "0.5px",
                    borderTopStyle: "solid",
                  }}
                >
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {t("total_earning")}
                  </h6>
                  <h6
                    className={style.fees_total}
                    style={{
                      fontWeight: "700",
                    }}
                  >
                    {computedData?.custom_obj?.total_earnings ?? 0}
                  </h6>
                </div>
              </div>
            </section>
            <div className={style.table_body}>
              {state.employee_ded?.map((gfh, index) => (
                <div className={style.table_body_list} key={index}>
                  <h6>{gfh?.head_name}</h6>

                  <h6>{gfh?.head_amount}</h6>
                </div>
              ))}
            </div>

            <div className={style.table_body}>
              <div
                className={style.table_body_list}
                style={{
                  borderTopColor: "rgba(18, 18, 18, 0.5)",
                  borderTopWidth: "0.5px",
                  borderTopStyle: "solid",
                }}
              >
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {t("total_pay")}
                </h6>
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {computedData?.custom_obj?.total_pay ?? 0}
                </h6>
              </div>
            </div>
            <div className={style.table_body}>
              {state.com?.map((gfh, index) => (
                <div className={style.table_body_list} key={index}>
                  <h6>{gfh?.head_name}</h6>
                  <h6>{gfh?.head_amount}</h6>
                </div>
              ))}
            </div>

            <div className={style.table_body}>
              <div
                className={style.table_body_list}
                style={{
                  borderTopColor: "rgba(18, 18, 18, 0.5)",
                  borderTopWidth: "0.5px",
                  borderTopStyle: "solid",
                }}
              >
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {t("net_pay")}
                </h6>
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {computedData?.custom_obj?.net_pay ?? 0}
                </h6>
              </div>
            </div>

            <div className={style.table_body}>
              {state.employar_ded?.map((gfh, index) => (
                <div className={style.table_body_list} key={index}>
                  <h6>{gfh?.head_name}</h6>
                  <h6>{gfh?.head_amount}</h6>
                </div>
              ))}
            </div>

            <div className={style.table_body}>
              <div
                className={style.table_body_list}
                style={{
                  borderTopColor: "rgba(18, 18, 18, 0.5)",
                  borderTopWidth: "0.5px",
                  borderTopStyle: "solid",
                }}
              >
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {t("ctc")}
                </h6>
                <h6
                  className={style.fees_total}
                  style={{
                    fontWeight: "700",
                  }}
                >
                  {computedData?.custom_obj?.ctc}
                </h6>
              </div>
            </div>
            <CreateButton label="finalize_and_save" onAction={onFinalize} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default SalaryComputationStaffComputedStructure;
